import * as React from "react"
import { graphql } from "gatsby"
import { FluidObject } from "gatsby-image"
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from "gatsby-source-contentful/rich-text"

import { BlogHeader } from "../../components/BlogHeader"
import { BlogBody } from "../../components/RenderRichContent"
import "../../styles/styles.scss"

type TemplateProps = {
  data: {
    blog: {
      id: string
      title: string
      slug: string
      date: string
      body: RenderRichTextData<ContentfulRichTextGatsbyReference>
      coverImages: {
        fluid: FluidObject
      }[]
    }
  }
}

const BlogTemplate = ({ data: { blog } }: TemplateProps) => {
  return (
    <div>
      Blog Header+ body
      {/* <BlogHeader
        image={blog.coverImages[0]}
        title={blog.title}
        date={blog.date}
      />
      <BlogBody content={blog.body} /> */}
    </div>
  )
}

export default BlogTemplate

export const query = graphql`
query {
  allContentfulHomepage(
    limit: 10
    filter: { node_locale: { eq: "nl-NL" } }
  ) {
    nodes {
      id
      node_locale
      maincontentleft {
        raw
      }
      maincontentright {
        raw
      }
      subheaderblock {
        raw
      }
      solutionstitle
      featured {
        slug
        title
        heroImage {
          file {
            details {
              image {
                height
                width
              }
            }
            url
          }
        }
        description {
          description
        }
      }
      solutions {
        slug
        title
        heroImage {
          file {
            details {
              image {
                height
                width
              }
            }
            url
          }
        }
        description {
          description
        }
      }
    }
  }
}
`
